var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('article',{staticClass:"bat-pavilion"},[_c('section',{staticClass:"bat-pavilion__ch bat-pavilion__ch--1"},[_c('h1',{staticClass:"bat-pavilion__ch-title"},[_vm._v("愛鳥 黃金蝙蝠的偶遇")]),_c('section',{staticClass:"bat-pavilion__ch-content"},[_c('p',[_vm._v("\n        黃金蝙蝠館的創辦人兼館長張恒嘉是北港人，在1985年離開大學後便在水林鄉誠正國小實習。「到水林就職的第一天，就在蘇秦衛生室看到黃金蝙蝠，從那天起我就會在戶外四處觀察蝙蝠的出沒！」張館長回憶起剛來水林時說道。\n      ")]),_c('img',{staticClass:"--center",attrs:{"src":(_vm.publicPath + "img/detail/bat_pavilion/蝙蝠俠張恆嘉館長.jpg"),"alt":"蝙蝠俠張恆嘉館長"}}),_c('p',[_vm._v("\n        水林和北港僅有十分鐘車程，他笑稱自己算是半個水林人，水林再熟悉不過，黃金蝙蝠的偶遇，開啟他對蝙蝠的研究的契機，直至成為今日的「黃金蝙蝠俠」。\n      ")])])]),_c('section',{staticClass:"bat-pavilion__ch bat-pavilion__ch--2"},[_c('h1',{staticClass:"bat-pavilion__ch-title"},[_vm._v("推廣 「預約幸蝠」的保育活動")]),_c('section',{staticClass:"bat-pavilion__ch-content"},[_c('p',[_vm._v("\n        張館長表示：「發現黃金蝙蝠之後，我查閱在台灣研究蝙蝠只有兩位專家學，於是開始調查水林蝙蝠的動態，2001年先是在誠正國小校區蘇秦衛生室內，設置木作蝙蝠屋開始，並進行蝙蝠分類及標放研究的分布調查。」在這個過程的研究發現許多金黃鼠耳蝠主要族群處，數量有下降的趨勢，因此張館長認為蝙蝠保育推動勢在必行。於是在2007年發起「預約幸蝠」的生態體驗活動，而得到了報名參加者的熱烈迴響。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/bat_pavilion/館長照顧的蝙蝠.jpg"),"alt":"館長照顧的蝙蝠"}}),_c('p',[_vm._v("\n        「“預約幸蝠”無疑是一個鼓舞信心的活動，為了永續經營，下一步如何找到一個長期的研究和推廣的基地，成為當時需要努力的目標。」張館長說。\n      ")])])]),_c('section',{staticClass:"bat-pavilion__ch bat-pavilion__ch--3"},[_c('h1',{staticClass:"bat-pavilion__ch-title"},[_vm._v("改造 把衛生室晉升為蝙蝠館")]),_c('section',{staticClass:"bat-pavilion__ch-content"},[_c('p',[_vm._v("\n        2008年當時張館長思考幾個候選基地時，由於「蘇秦衛生室」近鄰誠正國小的旁，無疑是最好的地點，且當時的使用機率偏低，館長拜訪了衛生室主任黃先瑞醫師，相談不到30秒黃醫師就爽快的提供空間讓他使用，黃金蝙蝠館在2009年正式啟用。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/bat_pavilion/蝙蝠屋的成品.jpg"),"alt":"蝙蝠屋的成品"}}),_c('p',[_vm._v("\n        「後來才得知黃醫師先前就參與過“預約幸蝠”，對我們所做的活動非常的認同。」張館長說。\n      ")])])]),_c('section',{staticClass:"bat-pavilion__ch bat-pavilion__ch--4"},[_c('h1',{staticClass:"bat-pavilion__ch-title"},[_vm._v("經營 台灣永續聯盟的協力支持")]),_c('section',{staticClass:"bat-pavilion__ch-content"},[_c('p',[_vm._v("\n        「成立保育和推廣基地，館內的營運，導覽培訓，辦活動等的人事費用，以及建物的維護管理的經費，都是營運一個文化館最大的挑戰！」張館長說。「台灣永續聯盟」一直持續與本館合作，以民間募資與派駐人力的方式維持館內的營運。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/bat_pavilion/規模最大蝙蝠屋.jpg"),"alt":"規模最大蝙蝠屋"}}),_c('p',[_vm._v("\n        近年來也陸續找到經費，2012年取得環境教育設施場所認證，蝙蝠館也逐漸從誠正國小而獨立出來，納入雲林縣政府管轄，與台灣永續聯盟共同經營管理，成為雲林縣推動環境教育的領頭羊。\n      ")])])]),_c('section',{staticClass:"bat-pavilion__ch bat-pavilion__ch--5"},[_c('h1',{staticClass:"bat-pavilion__ch-title"},[_vm._v("保育 向大眾推廣友善環境")]),_c('section',{staticClass:"bat-pavilion__ch-content"},[_c('p',[_vm._v("\n        「黃金蝙蝠館早期以團體與國中小的學生為主，後來在定位成環境教育文化館的同時，我們也逐漸把環境與生態保育的觀念，提升到一般大眾的環境教育推廣。」張館長表示。此外，和許多友善環境小農合作，在他們的田裡，提供慣行農法的農友設置蝙蝠屋，讓蝙蝠幫他吃掉害蟲，進而轉向有機農法的耕作，達到生態棲地共存的目的。\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/bat_pavilion/與友善環境農民合作的幸蝠米.jpg"),"alt":"與友善環境農民合作的幸蝠米"}}),_c('p',[_vm._v("\n        「幸蝠小舖」是蝙蝠館合作的夥伴，有些非營利團體和店家也長期跟蝙蝠館合作，除了販售有機產品之外，也有黃金蝙蝠相關的文創商品，希望更多人能了解蝙蝠和有機農業生產之間的關係，推廣生物多樣性保育的教育。\n      ")])])]),_c('section',{staticClass:"bat-pavilion__ch bat-pavilion__ch--6"},[_c('h1',{staticClass:"bat-pavilion__ch-title"},[_vm._v("努力 絕對不會背叛自己")]),_c('section',{staticClass:"bat-pavilion__ch-content"},[_c('p',[_vm._v("\n        黃金蝙蝠館成立至今已有10年，「一開始自己因為愛鳥的興趣，開始了蝙蝠的保育工作。」張館長笑稱。這過程我們了解到蝙蝠館營運的理想和現實的衝撞，由於張館長的全力投入，願意相信自己的選擇，用行動與毅力得到大家的認同，讓他人看見自己的努力，使越來越多人能從身邊周遭開始改變。終於這份力量聚沙成塔。\n      ")]),_c('p',[_vm._v("\n        張館長說：「生態保育的教育推廣，是一條漫長的道路！如何達到與自然共存、共榮的里山願景，是一個很難的挑戰，但卻必須趕緊去做，讓生活、生產與生態合為一體，以免後代子孫失去這些最寶貴的自然文化資產。」\n      ")]),_c('img',{staticClass:"--size-l --center",attrs:{"src":(_vm.publicPath + "img/detail/bat_pavilion/館長向民眾講解蝙蝠_黃金蝙蝠館提供.jpg"),"alt":"館長向民眾講解蝙蝠_黃金蝙蝠館提供"}})])])])}
